body {
  font-family: "Roboto", sans-serif;
  background-color: #b8b4ad;
}

.container {
  max-width: 1180px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}
@media (max-width: 767px) {
  .container {
    padding-left: 10px;
    padding-right: 10px;
  }
}

.row {
  margin-right: 0;
  margin-left: 0;
}

a {
  text-decoration: none;
  color: rgb(235, 235, 235);
}

.d-flex {
  display: flex;
}

.mt-3 {
  margin-top: 30px;
}

.mt-4 {
  margin-top: 40px;
}

.btn-red {
  background-color: #cc1b44;
  padding: 12px;
  margin: 22px 0 16px 0;
  border: none;
  border-radius: 2px;
  text-transform: uppercase;
  cursor: pointer;
  color: rgb(235, 235, 235);
}
.btn-red :hover {
  background-color: #e4365e;
}

.btn-green {
  background-color: #637140;
  padding: 12px;
  margin: 22px 0 16px 0;
  border: none;
  border-radius: 2px;
  text-transform: uppercase;
  cursor: pointer;
  color: rgb(235, 235, 235);
}
.btn-green :hover {
  background-color: #809252;
}

.header {
  padding: 10px 0;
  background-color: #000;
}
.header h1 {
  font-size: 1.5rem;
}
.header .menu {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.header .menu a {
